<template>
  <div class="summary-data-wrap">
    <div class="base-main-title">汇总列表&gt;</div>
    <div class="base-main-panel">
      <div class="control-bar">
        <div class="time-filter-box flex">
          <base-select class="year-select" placeholder="请选择年" :options="selectYearCount" :base-number="selectYearBase"></base-select>
          <base-select class="month-select" placeholder="请选择月" :options="12"></base-select>
          <span class="tips">提示：请同时选择年和月份后进行筛选</span>
        </div>
        <div class="btn-box flex">
          <button class="base-btn base-line-btn query-btn">查阅</button>
        </div>
      </div>
      <summary-data-table></summary-data-table>
      <base-pager :count="10"></base-pager>
    </div>
    <!-- <base-alert :show="true">
      <p>请同时选择三个条件后</p>
      <p>再进行“查阅”或“生成问题清单”操作</p>
    </base-alert> -->
    <!-- <base-alert :show="true">
      <div class="input-error-tips">
        <div class="tips-title">请输入正确的数字！</div>
        <div class="tips-content-wrap">
          <div class="tips-label">注意：</div>
          <div class="tips-content">
            <p>variance输入框数字为非负数，</p>
            <p>且小于全局variance；</p>
            <p>variance%输入框数字在0-100之间，</p>
            <p>且小于全局variance%</p>
          </div>
        </div>
      </div>
    </base-alert> -->

    <base-confirm class="build-dialog" title="生成问题清单" :show="confirmDisplay" @cancel="confirmDisplay=false" @sure="sure">
      <div class="form-item">
        <span class="form-label">variance</span>
        <input class="base-input form-input" type="text" placeholder="请输入数字" />
      </div>
      <div class="form-item">
        <span class="form-label">variance%</span>
        <input class="base-input form-input" type="text" placeholder="请输入 百分比" />
      </div>
    </base-confirm>
  </div>
</template>

<script>
import SummaryDataTable from '@/components/SummaryDataTable'
export default {
  data () {
    return {
      confirmDisplay: false
    }
  },
  computed: {
    selectYearBase () {
      return this.$config.selectBeginDate.getFullYear() - 1
    },
    selectYearCount () {
      return this.$config.selectEndDate.getFullYear() - this.selectYearBase
    }
  },
  methods: {
    sure () {
      this.confirmDisplay = false
      this.$dialog.alert({ msg: '填写正确', type: 'success' })
    }
  },
  components: {
    SummaryDataTable
  }
}
</script>

<style lang="scss" scoped>
.summary-data-wrap {
  .control-bar {
    @include flex();
    height: 72px;
    padding: 0 $margin-default;
    .time-filter-box {
      select,
      option {
        height: 22px;
      }
      .year-select,
      .month-select {
        margin-right: 8px;
        width: 90px;
      }
      .month-select {
        margin-right: 0;
      }
      .tips {
        margin-left: 16px;
        padding-left: 24px;
        line-height: 18px;
        color: $color-warning-text;
        position: relative;
        &:before {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          width: 18px;
          height: 18px;
          background: url(../assets/images/icons.png) 0 -40px no-repeat;
        }
      }
    }
    .btn-box {
      .query-btn {
        @include btn-size(80px, 25px);
      }
      .report-btn {
        @include btn-size(130px, 25px);
        margin-left: $margin-default;
      }
    }
  }
  .build-dialog {
    .form-item {
      @include flex($jus: center);
      .form-label {
        width: 75px;
        font-size: $font-default;
      }
      .form-input {
        width: 146px;
        height: 24px;
      }
      &:nth-child(n + 2) {
        margin-top: 14px;
      }
    }
  }
  .input-error-tips {
    .tips-label {
      padding-left: 20px;
      color: $color-error;
    }
    .tips-content-wrap {
      margin-top: 15px;
      font-size: 12px;
      @include flex($jus: center, $ali: flex-start);
      text-align: left;
    }
  }
}
</style>



